@import url('https://fonts.googleapis.com/css2?family=Oswald:wght@400;600;700&family=Poppins:wght@100;200;300;400;500&family=Roboto:ital,wght@0,300;0,400;0,500;1,100;1,300&family=Ubuntu:wght@300;400;500;700&display=swap');



/* font-family: 'Oswald', sans-serif;
font-family: 'Poppins', sans-serif;
font-family: 'Roboto', sans-serif;
font-family: 'Ubuntu', sans-serif; */

.all-link-page{
  display: flex;
  flex-direction: column;
  padding: 1rem;
}



.one-page,.two-page,.three-page {
  display: flex;
  width: 100%;
  height: auto;
  align-items: center;
  justify-content: space-around;
  margin: 0 auto;
}
   .two-page{
    flex-direction: row-reverse;
   }            
.one-page {
  background-image: linear-gradient(to right, #4776E6 0%, #8E54E9  51%, #4776E6  100%);
  /* margin: 10px; */
  /* margin-left: 12rem; */
  margin-top: 10px ;
  margin-bottom: 10px;
  padding: 15px 45px;
  text-align: center;
  /* text-transform: uppercase; */
  transition: 0.5s;
  background-size: 200% auto;
  color: white;            
  box-shadow: 0 0 10px #eee;
  border-radius: 10px;
  /* display: block; */
}
.one-page:hover {
  background-position: right center; /* change the direction of the change here */
  color: #fff;
  text-decoration: none;
}
   
         
.two-page {
  background-image: linear-gradient(to right, #43cea2 0%, #185a9d  51%, #43cea2  100%);
  /* margin-right: 12rem; */
  margin-top: 10px ;
  margin-bottom: 10px;
  padding: 15px 45px;
  text-align: center;
  /* text-transform: uppercase; */
  transition: 0.5s;
  background-size: 200% auto;
  color: white;            
  box-shadow: 0 0 10px #eee;
  border-radius: 10px;
}

.two-page:hover {
  background-position: right center; /* change the direction of the change here */
  color: #fff;
  text-decoration: none;
}


         
.three-page {
  background-image: linear-gradient(to right, #9D50BB 0%, #6E48AA  51%, #9D50BB  100%);
  /* margin-left: 12rem; */
  margin-top: 10px ;
  margin-bottom: 10px;
  padding: 15px 45px;
  text-align: center;
  /* text-transform: uppercase; */
  transition: 0.5s;
  background-size: 200% auto;
  color: white;            
  box-shadow: 0 0 10px #eee;
  border-radius: 10px;
  /* display: block; */
}

.three-page:hover {
  background-position: right center; /* change the direction of the change here */
  color: #fff;
  text-decoration: none;
}






.link-svg-1 img{
  margin: 0 auto;
  align-items: center;
  justify-content: center;
  margin-top: 1rem;
  width: 25rem;
  height: auto;
}


.btn1 a,.btn2 a{
    display: flex;
}
  .all-link-page:hover {
    background-position: right center;
    color: #fff;
    text-decoration: none;
  }
 
 


.link-page {
  display: flex;
  flex-direction: column;
  height: auto;
  text-align: center;
}
.profile-img {
  width: 100%;
  display: flex;
  height: auto;
  align-items: center;
  box-sizing: border-box;
  justify-content: center;
  padding: 1rem;
}
.profile-img img {
  width: 120px;
  height: auto;

}
.link-title h2{
  margin-bottom: 1rem;
     font-size: 1.2rem;
    color: white;
    font-family: 'Oswald', sans-serif
}
.link-header h1{
    color: white;
    font-family: 'Poppins', sans-serif;
    font-size: 1.3rem;
}
.link-content{
    color: white;
    font-family: 'Ubuntu', sans-serif;
    font-size: 0.9rem;
}


.link-page-btn {
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 100%;
  height: auto;
}
.btn1 a,
.btn2 a {
  color: rgb(255, 255, 255);
  text-decoration: none;
}

         
         
         
         
.btn1,.btn2 {
  background-image: linear-gradient(to right, #6a3093 0%, #a044ff  51%, #6a3093  100%);
  margin: 10px;
  padding: 10px 45px;
  text-align: center;
  /* text-transform: uppercase; */
  transition: 0.5s;
  background-size: 200% auto;
  color: white;            
  box-shadow: 0 0 10px #eee;
  border-radius: 10px;
  display: block;
}

.btn1:hover,.btn2:hover {
  background-position: right center; /* change the direction of the change here */
  color: #fff;
  text-decoration: none;
}

 
 
 
@media (min-width: 0px) and (max-width:896px){
  
.all-link-page{
  flex-direction: column;
}
.one-page,.two-page,.three-page{
  flex-direction: column;
}
.one-page img,.two-page img,.three-page img{
  width: 300px;
  height: auto;
}
.profile-img img{
  width: 120px;
}
.link-page-btn{
  flex-direction: column;
}
}


