.line{
    border-top: 0.1px solid black;
    padding: 1rem;
}

.project-main-content{
    display: flex;
    width: 100%;
    height: auto;
}
.project-v{
    width: 49%;
    height: auto;
    align-content: center;
    justify-content: space-evenly;
    display: flex;
}
/* .project-v{
    width: 50%;
    height: auto;
} */
.card-1{
    margin-right: 1rem;
}
.project-v video,.card-1 video,.card-2 video{
    width: 100%;
    height: auto;
    border-top-left-radius: 1rem;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}
         
.link{
    background-image: linear-gradient(to right, #00c6ff 0%, #0072ff  51%, #00c6ff  100%);
    margin: 10px;
    padding: 15px 45px;
    text-align: center;
    text-transform: uppercase;
    transition: 0.5s;
    background-size: 200% auto;
    color: rgb(255, 255, 255);            
    box-shadow: 0 0 20px #eee;
    border-radius: 10px;
    display: block;
  }

  .link:hover {
    background-position: right center; /* change the direction of the change here */
    color: rgb(0, 0, 0);
    text-decoration: none;
  }
 
/* .bi-arrow-right{
    margin-right: 1rem;
} */
.card-1,.card-2{
    display: inline-block;
    width: 18rem;
    height: auto;
}
.card-1 img,.card-2 img{
    border-radius: 0.3rem;
    width: 17rem;
    height: 17rem;
}
.card-body h1{
    margin-top: 1rem;
    font-size: 2rem;
    font-family: 'Poppins', sans-serif;
}
.card-body p{
    font-size: 0.9rem;
    /* font-weight: 400; */
    font-family: 'Montserrat', sans-serif;
}
.arrow-btn{
    margin: 2rem auto;
}
.comment,.text-summary{
    font-family: sans-serif;
    font-weight: 600;
    display: flex;
    margin: 3rem auto;
    padding-left: 3rem;
    padding-right: 3rem;
  padding-top: 3rem;
    flex-direction: column;
    width: 100%;
    height: auto;
}
.btn-box{
    width: 100%;
    height: auto;
    margin: 0 auto;
    align-items: center;
    justify-content: space-around;
    display: flex;
}

/* ------------------Project1-start */
.project1{
    width: 100%;
    height: auto;
}
.project1 img{
    width: 100%;
    height: auto;
}



/* ------------------Project1-end */























@media (min-width: 0px) and (max-width:896px){
.project-main-content{
    width: 100%;
    display: flex;
    flex-direction: column;
}
.project-v{
    width: 18rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: auto;
    margin: 0 auto;
}
.project-v video{
    min-width: 300px;
    margin-bottom: 1rem;
    border-radius: 0px;
}
.project-list{
 
    margin: 0 auto;
}
.card-1,.card-2{
    width: 18rem;
}
.project-list h1{
    margin-top: 1rem;
}
.btn-box{
    display: flex;
    flex-direction: column;
}
.btn{
    margin-top: 1rem;
}
}