.education-header,.experience-header,.project-header,.digital-header,.news-header{
    display: flex;
    margin: 1rem auto;
}
.education-header h1,.experience-header h1,.project-header h1,.digital-header h1,.news-header h1{
    font-size: 2.5rem;
    font-weight: 700;
    font-family: 'Fredoka One', cursive;
    letter-spacing: 0.5rem;
    padding: 1rem 2rem; 
    background-color: black;
    color: white;
}

.education-list,.experience-list{
    display: flex;
    width: 90%;
    height: auto;
    margin: 0 auto;
}
.list{
    width: 16rem;
    margin: 0 auto;
}
.list-color h2{
    letter-spacing: 0.1rem;
    font-size: 1.5rem;
}
.arrow-btn{
    width: 80%;
    height: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
}
.left-btn,.right-btn{
    padding: 0 1rem;
    font-size: 2rem;
    cursor: pointer;

}


/* -----------------education-page-1-start------------ */
.education-page-1{
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: column;
   margin: 1rem auto;
}
.main-page-1{
    width: 80%;
    height: auto;
    display: flex;
    align-items: center;
    justify-content: space-around;
    margin: 0 auto;
}
