.certificate{
    display: flex;
    width: 100%;
    margin: 0 auto;
    border-top-right-radius: 2rem;
    border-top-left-radius: 2rem;
    border-bottom-left-radius: 2rem;
    border-bottom-right-radius: 18rem;
    background-image: linear-gradient(to right, #00C9FF 0%, #92FE9D  51%, #00C9FF  100%);
    margin: 10px;
    padding: 15px 45px;
    text-align: center;
    text-transform: uppercase;
    transition: 0.5s;
    background-size: 200% auto;
    color: rgb(0, 0, 0);            
    box-shadow: 0 0 20px #eee;
    border-radius: 10px;
    /* display: block; */
  }
  .certificate h1{
    margin: 0 auto;
  }

  .certificate:hover {
    background-position: right center; /* change the direction of the change here */
    color: #fff;
    text-decoration: none;
  }
 
.skill h1{
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    width: 90%;
    box-shadow: rgb(85, 91, 255) 0px 0px 0px 3px, rgb(31, 193, 27) 0px 0px 0px 6px, rgb(255, 217, 19) 0px 0px 0px 9px, rgb(255, 156, 85) 0px 0px 0px 12px, rgb(255, 85, 85) 0px 0px 0px 15px;
}
.skill-paragraph{
    width: 90%;
    margin: 2rem auto;
}
.certificate h1{
    border-radius: 1rem;
}
.component-page-bg{

    border-top-left-radius: 18rem;
    /* border-bottom-right-radius: 18rem; */
background-color: #8EC5FC;
background-image: linear-gradient(62deg, #8EC5FC 0%, #E0C3FC 100%);

    width: 100%;
}
.component-page{
    display: flex;
    width: 80%;
    height: auto;
    margin: 3rem auto;
    align-items: center;
    justify-content: space-between;
    /* background-color: #8EC5FC; */
}
.skill-card{
    width: 18rem;
    height: 400px;
    border: 1px solid rgb(202, 191, 191);
background-image: linear-gradient(to right, #00b09b, #96c93d);
border-bottom-left-radius: 1rem;
border-bottom-right-radius: 1rem;

}
.skill-img img{
    width: 18rem;
}
.skill-content{
    text-align: center;
    padding-top: 2rem;
    padding-left: 1rem;
}    
.skill-link a {
    background-image: linear-gradient(to right, #fc00ff 0%, #00dbde  51%, #fc00ff  100%);
    margin: 10px;
    padding: 15px 45px;
    text-align: center;
    text-transform: uppercase;
    transition: 0.5s;
    background-size: 200% auto;
    color: white;            
    box-shadow: 0 0 20px #eee;
    border-radius: 10px;
    display: block;
  }

  .skill-link a:hover {
    background-position: right center; /* change the direction of the change here */
    color: #fff;
    text-decoration: none;
  }
 
/* -----------------education-page-1-end------------ */






@media (min-width: 0px) and (max-width:896px){
    
    .education-header h1,.experience-header h1,.project-header h1{
        font-size: 2rem;
    }
    .education-list,.experience-list{
        width: 100%;
        height: auto;
        align-items:center;
        justify-content: space-around;
        display: flex;
        flex-direction: column;
    }
    .list-item-1,.list-item-2{
        width: 90%;
        height: auto;
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        /* padding: 1rem; */
        
    }
    .list{
        /* border: 1px solid black; */
        margin-top: 1rem;
        padding: 1rem;
    }
    .list h3{
        font-size: 1.5rem;
    }
    .list p{
        font-size: 1rem;
        font-weight: 300;
    }
    .list h2{
        font-size: 2rem;
        font-weight: 600;
    }
    .main-page-1{
        flex-direction: column;
    }
    .main-page-1.col2 img{
        width: 360px;
        height: auto;
    }
    .component-page{
        flex-direction: column;
    }
    .skill-card{
        margin-bottom: 1rem;
    }
    .col2 img,.certificate{
        width: 300px;
    }
    .certificate h1{
        font-size: 0.9rem;
        
    }
}