.about-page{
    display: flex;
    width: 100%;
    /* height: 73vh; */
    height: auto;
    margin: 2rem auto;
    justify-content: space-around;
    box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
    /* background-color: #4158D0;
background-image: linear-gradient(43deg, #4158D0 0%, #C850C0 46%, #FFCC70 100%); */
background-image: radial-gradient( circle farthest-corner at 10% 20%,  rgba(255,94,247,1) 17.8%, rgba(2,245,255,1) 100.2% );
    border-bottom-right-radius: 40rem;
}

.about-page-1 img{
    width: 500px;
    height: auto;
    border-right: 2rem solid #C850C0;
    border-bottom:1rem solid #C850C0;
    border-bottom-right-radius: 3rem;
    border-top-left-radius: 3rem;
    transition: ease-in-out 1s;
    padding: 1.5rem;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
    
}
.about-page-1 img:hover{
    border-left: 2rem solid #000000;
    border-top: 1rem solid #000000;
    border-right: none;
    border-bottom: none;
}
.about-page-2{
    width: 500px;
    /* padding: 0 2rem; */
}
.about-page-2 h1{
    font-size: 3rem;
   padding: 1rem;
    letter-spacing: 0.2rem;
    font-weight: 600;
    background-color: black;
    color: white;
    font-family: 'Combo', cursive;
}

.about-page-2 span{
   color: black;
   font-size: 3rem;
   letter-spacing: 0.2rem;
   font-weight: 500;
   font-family: 'Fredoka One', cursive;
}
.my-title{
    /* width: 100%; */
    /* padding: 1rem; */
    margin-top: 1rem;
    /* background-color: #C850C0; */
    font-family: 'Combo', cursive;
}
.about-page-2 p{
    margin-top: 1rem;
    font-family: 'Poppins', sans-serif;
    font-weight: 400;
}
.about-list{
    display: flex;
    align-items: center;
}
.list-1 h3,.list-2 h3{
    display: flex;
    padding: 0.5rem;
    font-size: 1.3rem;
}
.fa-pen-ruler,.fa-laptop-code,.fa-pen-fancy,.bi-menu-button-wide-fill{
    margin-right: 0.8rem;
    padding: 0.2rem;
    background-color: #00DBDE;
background-image: linear-gradient(90deg, #00DBDE 0%, #FC00FF 100%);

}
.cv{
    margin-top: 1rem;
}         
 .cv a {
        width: 50%;
        background-image: linear-gradient(to right, #00c6ff 0%, #0072ff  51%, #00c6ff  40%);
        margin: 10px;
        padding: 10px 30px;
        text-align: center;
        text-transform: uppercase;
        transition: 0.5s;
        background-size: 200% auto;
        color: rgb(0, 0, 0);            
        box-shadow: 0 0 20px #eee;
        border-radius: 10px;
        display: block;
      }

      .cv a:hover {
        background-position: right center; /* change the direction of the change here */
        color: rgb(0, 0, 0);
        text-decoration: none;
      }
    
@media (min-width: 0px) and (max-width:896px){
    .about-page{
        display: flex;
        flex-direction: column;
        width: 100%;
        justify-content: center;
        align-items: center;
        margin: 1rem auto;
        /* height: 110vh; */
        height: auto;
    }
    .about-page-1 img{
        width: 70%;
        height:auto;
    }
    .about-page-2{
        width: 100%;
        height: auto;
    }
    .about-page-2 h1{
        font-size: 2rem;
    }
    .about-page-2 span{
        font-size: 1rem;
        padding: 0.5rem;
    }
    .about-page-2 p{
        width: 80%;
        /* margin: 0 auto; */
        padding: 0 0.5rem;
        font-size: 0.8rem;
    }
    .list-1 h3,.list-2 h3{
        font-weight: 500;
        font-size: 0.8rem;
    }
    .cv a{
        width: 270px;
        font-size: 0.8rem;
    }
}