
.footer{
    width: 100%;
    height: auto;
    display: flex;
    justify-content: space-between;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    margin: 0 auto;
    padding: 1rem 2rem;
   
}

         
.footer{
    background-image: linear-gradient(to right, #1A2980 0%, #26D0CE  51%, #1A2980  100%);
    padding: 15px 45px;
    text-align: center;
    transition: 0.5s;
    background-size: 200% auto;
    color: white;            
    box-shadow: 0 0 10px #eee;
  }

  .footer:hover {
    background-position: right center; 
    color: #fff;
    text-decoration: none;
  }
 


.footer-left,.footer-mid,.footer-right{
    display: flex;
    margin: 0 auto;
}
.footer-left{
    width: 30%;
    height: auto;
    display: flex;
    flex-direction: column;
}
.email-link{
    background-color: white;
    border-radius: 2rem;
    outline: none;
justify-content: center;
display: flex;
}
.email-link a{
    width: 200px;
    color: rgb(255, 255, 255);
    text-decoration: none;
    padding: 0.5rem 2rem;
    background-color: black;
    border-radius: 1rem;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;

}
.social-link{
    display: flex;
    width: 100%;
    margin: 1rem auto;
}
.social-link ul li{
    list-style: none;
}
.social-link ul li a{
   padding: 0 1rem;
   font-size: 1.5rem;
   color: rgb(0, 0, 0);
}
.social-link ul li a:hover{
    color: #35063e;
}
.footer-left input{
    width: 70%;
    height: auto;
    outline: none;
    border: none;
}
.footer-mid{
    width: 33%;
   height: auto;
   justify-content: center;
   margin: 0 auto;
    display: flex;

}
.footer-mid-logo img{
    width: 120px;
    height: auto;
    margin-right: 1rem;
}
.footer-mid-logo h1{
    display: flex;
    margin: 1rem auto;
    align-items: center;
    justify-content: center;
    font-size: 1.5rem;
    font-weight: 600;
    color: white;
}
.paragraph p{
    font-size: 0.9rem;
    color: white;
    line-height: 1.6rem;
}
   
.paragraph-more {
    background-image: linear-gradient(to right, #4776E6 0%, #8E54E9  51%, #4776E6  100%);
    margin: 10px;
    padding: 15px 45px;
    text-align: center;
    text-transform: uppercase;
    transition: 0.5s;
    background-size: 200% auto;
    color: white;            
    box-shadow: 0 0 10px #eee;
    border-radius: 10px;
    display: block;
  }

  .paragraph-more:hover {
    background-position: right center; /* change the direction of the change here */
    color: #fff;
    text-decoration: none;
  }
 
.contact p{
color: white;
}
.footer-right{
    width: 20%;
    height: auto;
    /* margin: 0 auto; */
    display: flex;
    flex-direction: column;
}
.footer-right maps{
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}

.contact p{
    margin-top: 1rem;
    font-size: 1rem;
   
}
.contact a{
    color: white;
}
/* ------------------contact page-start----------------------- */
.contact-main{
    background-color: #4158D0;
background-image: linear-gradient(43deg, #4158D0 0%, #C850C0 46%, #FFCC70 100%);

}

.contact-page{
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    
}
.c-page-1{
    display: flex;
    margin: 0 auto;
    align-items: center;
    justify-content: center;
    width: 100%;
    /* padding: 0 2rem; */
    font-family: 'Fredoka One', cursive;
    
}
.c-page-1 img{
    width: 50%;
}

.c-page-2{
    width: 90%;
    padding: 0 4rem;
    display: flex;
    flex-direction: column;
    font-family: 'Fredoka One', cursive;
}
.c-page-2 input{
    padding: 0.8rem;
    margin-top: 0.5rem;
    margin-bottom: 0.8rem;
    border-radius: 2rem;
    outline: none;
    border: none;
    box-shadow: rgba(0, 0, 0, 0.07) 0px 1px 2px, rgba(0, 0, 0, 0.07) 0px 2px 4px, rgba(0, 0, 0, 0.07) 0px 4px 8px, rgba(0, 0, 0, 0.07) 0px 8px 16px, rgba(0, 0, 0, 0.07) 0px 16px 32px, rgba(0, 0, 0, 0.07) 0px 32px 64px;
}
.c-page-2 textarea{
    box-shadow: rgba(0, 0, 0, 0.07) 0px 1px 2px, rgba(0, 0, 0, 0.07) 0px 2px 4px, rgba(0, 0, 0, 0.07) 0px 4px 8px, rgba(0, 0, 0, 0.07) 0px 8px 16px, rgba(0, 0, 0, 0.07) 0px 16px 32px, rgba(0, 0, 0, 0.07) 0px 32px 64px;
}
.c-maps iframe{
    background-color: rgb(255, 255, 255);
    color: black;
    width: 40vw;
    height: auto;
}
.submit{
    width: 100%;
    display: block;
    border: none;
    background-color: #000000;
    box-shadow: rgba(240, 46, 170, 0.4) 0px 5px, rgba(240, 46, 170, 0.3) 0px 10px, rgba(240, 46, 170, 0.2) 0px 15px, rgba(240, 46, 170, 0.1) 0px 20px, rgba(240, 46, 170, 0.05) 0px 25px;
}
/* ------------------contact page-end----------------------- */







@media (min-width:0) and (max-width:768px){
    .footer{
        width: 100%;
        display: flex;
        flex-direction: column;
    }
    .footer-left,.footer-mid,.footer-right{
        width: 100%;
        height: auto;
    }
    .social-link ul li{
        width: 100%;
        display: flex;
    }
    .email-link a{

        width: 50%;
        font-size: 0.7rem;
    }
    .footer-mid{
        align-items: center;
        display: flex;
        flex-direction: column;
    }
    .footer-mid-logo{
        width: 100%;
        display: flex;
        justify-content: space-around;
        margin-bottom: 1rem;
    }
  .footer-right{
    margin-top: 2rem;
  }
  .contact-page{
    display: flex;
    flex-direction: column;
}
}